

.animation-container {
    width: 200px;
    height: 200px;
    background-size: cover;
    vertical-align: middle;
    margin: 20% auto;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.line {
    height: 3px;
    width: 100%;
    background: #e8e8e8;
    margin-top: 30%;
}
.line .inner {
    width: 100%;
    height: inherit;
    background: #0077b5;
    animation: slide 2s ease-in-out infinite;
}

@keyframes slide {
    0% {
        transform-origin: left;
        transform: scalex(0.3);
    }
    25% {
        transform-origin: left;
        transform: scalex(1);
    }
    26% {
        transform-origin: right;
        transform: scalex(1);
    }
    50% {
        transform-origin: right;
        transform: scalex(0.3);
    }
    75% {
        transform-origin: right;
        transform: scalex(1);
    }
    76% {
        transform-origin: left;
        transform: scalex(1);
    }
    100% {
        transform-origin: left;
        transform: scalex(0.3);
    }
}
